import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';
import { MeasurementAndDosageSizesService } from './../../services/MeasurementAndDosageSizesService';
import swal from 'sweetalert2';

@Component({
  selector: 'add-dosage',
  templateUrl: './add-dosage.component.html',
  styleUrls: [
    './add-dosage.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [MeasurementAndDosageSizesService]
})
export class AddDosageComponent implements OnInit {
  rowsBasic = [];
  fullScreenRow = [];
  loadingIndicator = true;
  reorderable = true;
  Dosage: any = {};
  Dosagee: any = [];
  Dosage2: any = {};
  myValue;

  columns = [
    { prop: 'name' },
    { name: 'Gender' },
    { name: 'Company' }
  ];

  rows = [];
  expanded = {};
  timeout: any;

  rowsFilter = [];
  tempFilter = [];

  public dosages_array = [];
  @ViewChild(DatatableComponent) table: DatatableComponent;


  constructor(public _addMeasurementServices: MeasurementAndDosageSizesService, public _addDosageServices: MeasurementAndDosageSizesService) {
    this.getDosages();
  }
  ngOnInit() {
  }

  getDosages() {
    this._addMeasurementServices.getDosage().subscribe(response => {
      if (response.code == 200) {
        this.dosages_array = response.data;
        this.tempFilter = this.dosages_array;
        this.rowsFilter = this.dosages_array;
      }
    });
  }// Get Measurements End

  fetchBasicData(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', 'assets/data/basic.json');

    req.onload = () => {
      cb(JSON.parse(req.response));
    };

    req.send();
  }

  fetchFullScreenData(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', 'assets/data/fullscreen.json');

    req.onload = () => {
      cb(JSON.parse(req.response));
    };

    req.send();
  }

  fetchFilterData(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', `assets/data/company.json`);

    req.onload = () => {
      cb(JSON.parse(req.response));
    };

    req.send();
  }


  updateFilter(event) {
    const val = event.target.value.toLowerCase();

    // filter our data
    const temp = this.tempFilter.filter(function (d) {
      return d.UnitName.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rowsFilter = temp;
    // Whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }

  onPage(event) {
    clearTimeout(this.timeout);
    this.timeout = setTimeout(() => {
      console.log('paged!', event);
    }, 100);
  }

  fetch(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', `assets/data/100k.json`);

    req.onload = () => {
      const rows = JSON.parse(req.response);

      for (const row of rows) {
        row.height = Math.floor(Math.random() * 80) + 50;
      }

      cb(rows);
    };
    req.send();
  }

  getRowHeight(row) {
    return row.height;
  }
  validation() {
  
  }
  //services
  addDosage = {};
  addDosageServices() {
    this._addDosageServices.addDosageAndSizeService(this.Dosage.name, this.Dosage.symbol).subscribe(data => {
      this.dosages_array.push(data.data);
      this.Dosage = {};
    });
  }
  //Delete Services
  deleteMeasurementServices(i) {
    console.log(i);
    var self = this;
    swal({
      title: 'Are you sure?',
      text: 'You wont be able to revert',
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!'
    }).then(function (result) {
      if (result.value == true) {
        self._addMeasurementServices.deleteDosage(i).subscribe(response => {
          var index_number = self.dosages_array.findIndex(x => x.Id == i);
          self.dosages_array.splice(index_number, 1);
          self.rowsFilter = self.dosages_array;
          self.tempFilter = self.dosages_array;
          swal(
            'Deleted!',
            'Package has been deleted.',
            'success'
          );
        });
      }
      else {
      }
    }).catch(swal.noop);
  }
  //edit function
  editDosage(dosage) {
    this.Dosage2 = dosage;
    $('#showIt').show();
    console.log(dosage);
  }
  public dosage_id: number = 0;
  openMyModal(Id){
    this.dosage_id = Id;
    var obj = this.dosages_array.find(x=>x.Id == Id);
    this.Dosage2.name = obj.UnitName;
    this.Dosage2.symbol = obj.UnitSymbol;
  }
  updateDosage(){
    var index_number = this.dosages_array.findIndex(x => x.Id == this.dosage_id);
    this.dosages_array[index_number].UnitName = this.Dosage2.name;
    this.dosages_array[index_number].UnitSymbol = this.Dosage2.symbol;
    this._addMeasurementServices.updateDosage(this.dosage_id, this.Dosage2.name, this.Dosage2.symbol).subscribe(response => {});
    this.rowsFilter = this.dosages_array;
    this.tempFilter = this.dosages_array;
  }
}



